
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
  components: {
    XInput,
    Group,
    XButton,
    Cell,
    Flexbox,
    FlexboxItem,
  },
  data() {
    return {
      viewControl: {
        activeName: 'active',
        nowType: -1,
        showBoxTypeName: 'type3',
        nameLong: 0
      },
      data: {
        type: 'none'
      },
      type: '',
    }
  },
  methods: {
    changeType(val){
      this.viewControl.nowType = Number(val);
      this.viewControl.showBoxTypeName = 'type' + Number(val);
    },
    $doSubmit(){
      if (this.viewControl.nowType == 0) {
        this.data.type = "three";
      } else if (this.viewControl.nowType == 1) {
        this.data.type = "all";
      } else { //无
        this.data.type = "none";
      }

      var _self = this;
      this.H.toAjax(true).then(function (comData) {
        comData.data = {
          activityId: _self.$route.query.id,
          type: _self.data.type,
        };
        _self.A.post("/v1/attendance/previewType", comData).then(function (result) {
          console.log(result.data.state.msg);
          if (result.data.state.code == 0) {
            _self.$emit("on-hide",_self.data.type);
            _self.$router.push({path:'/admin/presonal', query: {id: _self.$route.query.id, type: _self.$route.query.type}});
          }
        });
      });
    }
  },
  mounted() {

    document.getElementById("title").innerHTML = "预览设置";
    var _self = this;
    this.H.toAjax(true).then(function (comData) { //data 公用参数
      comData.data = {
        activityId: _self.$route.query.id,
      };
      _self.A.post("/v1/attendance/detail", comData).then(function (result) {
      //debugger ;
        //console.log(result.data.data.attendance.previewType);
        if (result.data.state.code == 0) {

          var previewType = result.data.data.attendance && result.data.data.attendance.previewType ? result.data.data.attendance.previewType : 2 ;
          if (previewType == "three") {
            _self.viewControl.nowType = 0;
          } else if (previewType == "all") {
            _self.viewControl.nowType = 1;
          } else {
            _self.viewControl.nowType = 2;
          }
          _self.viewControl.showBoxTypeName = 'type' + _self.viewControl.nowType;

        } else {
          _self.$emit("modalAlert", result.data.state.msg, "确定");
        }
      });
    });
  }
}



