      type: type,
      page: page
    }
  }
  var starObj = function (ids) { //点赞列表
    return {
      momentIds: ids,
      page: {
        size: 50,
        lastValue: 0
      }
    }
  }
  var commentObj = function (ids, time, limit) {
    return {
      momentIds: ids,
