    methods: {
      playVoice(audio, momentIndex){
        var _self = this;
        var _second = audio.second * 1000;
        if(audio.url) {
          this.mediaData.isPlaying = true;
          this.mediaData.playingIndex = momentIndex;
          if(!this.mediaData.audioEle) {
            this.mediaData.audioEle = document.querySelector("#audioPlayer");
          }
          var audioEle = this.mediaData.audioEle;

          //播放完成后停止动画
          this.mediaData.playerTimer = setTimeout(function () {
            _self.mediaData.isPlaying = false;
          },_second);
          audioEle.volume = 0;
          if(audioEle.src != '' || audioEle.src || audioEle.currentSrc) {
            audioEle.pause();
            audioEle.src = '';
          }
          audioEle.src = _self.mediaData.mp3UrlPerfix + audio.url + "?again=" + new Date().getTime();
          audioEle.play();
          var temp = setTimeout(function(){
            audioEle.volume = 1;
            audioEle.play();
          },300);

        } else {
          alert("播放失败");
        }
      },
      stopVoice(audio, momentIndex){
        console.log(audio,momentIndex);
        var _self = this;
        clearTimeout(_self.mediaData.playerTimer);
        _self.mediaData.playerTimer = null;

        if(audio.url) {
          if(!_self.mediaData.audioEle) {
            _self.mediaData.audioEle = document.querySelector("#audioPlayer");
          }
          var audioEle = this.mediaData.audioEle;
          audioEle.volume = 0;
          audioEle.pause();
          audioEle.currentTime = 0;
//          audioEle.src = '';
//          _self.mediaData.src = '';
          _self.mediaData.isPlaying = false;

        } else {
          _self.mediaData.isPlaying = false;
        }
      },
