          console.info("punchData",this.punchData);
          this.applyCardData.id = this.punchData.activityId;
          this.applyCardData.startTime = this.punchData.startTime;
          this.applyCardData.creator = this.punchData.creator;
          this.applyCardData.name = this.punchData.name;
          this.applyCardData.runRenderMethod = true;

          //如果没有数据则请求一次
        } else {
          var _self = this ;
          this.H.toAjax(true).then(function(comData){ //data 公用参数
