      //评论
      $doSubmitComment(item){
         var _el = findMomentObj.call(this, 'momentId', "commentResult", this.viewControl.momentId, this.momentList); //当前操作的动态
         console.log("_el", _el);
         var momentId = this.viewControl.momentId, select = this.selectedObj;
         var obj = {},
         comment = this.viewControl.content;
         if(!comment ){
          this.$emit("$modalAlert","输入不能为空");
          return ;
        }else if(comment.length > 100){
          this.$emit("$modalAlert","字数不能大于100");
          return ;
        }
        this.viewControl.content = ""; // 清空表单
        if(this.viewControl.toSomeone) { //回复someone
          obj = doCommentObj(momentId, comment, this.viewControl.commentId);
          this.viewControl.toSomeone = false;
        }else{
          obj = doCommentObj(momentId, comment);
          delete obj.commentId; //自己评论版主
        }
        var _self = this;
        this.H.toAjax(true)
          .then(function (comData) { //data 公用参数
            comData.data = obj;
            _self.A.post("/v1/comment/add", comData)
            .then(function (result) {
              if (result.data.state.code == 0) {

