      //重新设置userCode
      var _self = this;
      this.H.toAjax(true).then(function(comData) { //data 公用参数
        var data = {
          id: _self.$route.query.id,
          type: _self.$route.query.type
        }
        comData.data = data;
        _self.A.post("/v1/userbind/clientUserCode", comData)
          .then(function (result) {
            if(result.status == 0){
              M.F.D.set("userCode",result._data.userCode);
              M.userCode = result._data.userCode;
            }
          });
      });
    }
  }



