;\n  overflow: hidden;\n  opacity: 0;\n  display: block;\n}\n/*盒子模型*/\n.border-box {\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n}\n.content-box {\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n}\n/*常用浮动与定位*/\n.float-left {\n  float: left;\n}\n.float-right {\n  float: right;\n}\n.pos-real {\n  position: relative;\n}\n.pos-absl {\n  position: absolute;\n}\n/*字体颜色*/\n.font-c-red {\n  color: #a60000;\n}\n.font-c-blue {\n  color: #0E90E0;\n}\n.font-c-gray {\n  color: #8C9195;\n}\n.font-c-black {\n  color: #262626;\n}\n.font-c-Lblack {\n  color: #414141;\n}\n.font-c-gray2 {\n  color: #8f8f8f;\n}\n/*字体对齐*/\n.txt-left {\n  text-align: left;\n}\n.txt-right {\n  text-align: right;\n}\n.txt-cent {\n  text-align: center;\n}\n.ver-mid {\n  vertical-align: middle;\n}\n/*字体大小*/\n.f0 {\n  font-size: 0;\n}\n.f12 {\n  font-size: 12px;\n}\n.f13 {\n  font-size: 13px;\n}\n.f14 {\n  font-size: 14px;\n}\n.f15 {\n  font-size: 15px;\n}\n.f16 {\n  font-size: 16px;\n}\n.f17 {\n  font-size: 17px;\n}\n.f18 {\n  font-size: 18px;\n}\n/*顶部空隙*/\n.has-top {\n  margin-top: 15px;\n}\n.has-top2 {\n  padding-top: 15px;\n}\n/*左右空隙*/\n.has-side {\n  padding: 0 15px;\n}\n.has-outSide {\n  margin: 0 15px;\n}\n/*底部空隙*/\n.has-bottom {\n  margin-bottom: 15px;\n}\n.has-bottom2 {\n  padding-bottom: 15px;\n}\n/*隐藏多余字体*/\n.txt-hide {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n/*显示与隐藏*/\n.show {\n  display: block !important;\n}\n.inline-show {\n  display: inline-block;\n}\n.flex-show {\n  display: -webkit-box;\n  display: -webkit-flex;\n  display: -ms-flexbox;\n  display: flex;\n}\n.hide {\n  display: none !important;\n}\n.over-hide {\n  overflow: hidden;\n}\n/*边框*/\n.border-top {\n  border-top: 1px solid #d9d9d9;\n}\n.border-bottom {\n  border-bottom: 1px solid #d9d9d9;\n}\n.border-all {\n  border: 1px solid #d9d9d9;\n}\n/*边框颜色*/\n/*******************************************************\n  模块样式\n*******************************************************/\n/*白色基本块*/\n.form-block {\n  background-color: #FFFFFF;\n  border-bottom: 1px solid #d9d9d9;\n}\n.form-block.form-hasPadding {\n  padding: 0 15px;\n}\n.form-block.form-hasTop {\n  margin-top: 15px;\n}\n.form-block.form-paddBotm {\n  padding-bottom: 15px;\n}\n/*通用大标题*/\n.normal-title {\n  height: 40px;\n  line-height: 40px;\n  font-weight: 400;\n  padding: 0 15px;\n  font-size: 16px;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n}\n/*一般表单顶部标题*/\n.inputTitle {\n  height: 34px;\n  line-height: 34px;\n  padding-top: 5px;\n  font-weight: 300;\n  font-size: 15px;\n}\n.inputTitle .rightCount {\n  float: right;\n  height: 100%;\n  line-height: inherit;\n  font-weight: 400;\n  color: #8f8f8f;\n}\n.inputTitle .rightCount.addTopic {\n  font-size: 0;\n}\n.inputTitle .rightCount.addTopic:after {\n  content: \" \";\n  display: block;\n  width: 15px;\n  height: 34px;\n  background: url(\"../../../../src/assets/img/topicAdd-btn.png\") right center no-repeat;\n  background-size: 15px 15px;\n}\n/*一般表单*/\n.input.input-hasTop {\n  margin-top: 15px;\n}\n.input .weui-cells {\n  margin-top: 0;\n  border: 1px solid #d9d9d9;\n}\n.input .weui-cells:before,\n.input .weui-cells:after {\n  display: none;\n}\n.input .weui-cells .weui-cell {\n  padding: 7px 5px 7px;\n}\n.input .weui-cells .weui-cell .weui-input {\n  font-size: 14px;\n}\n/*标题在同一行的表单*/\n.input-hasTitle .input-title {\n  -webkit-box-flex: 0;\n              -ms-flex: 0 0 74px;\n          flex: 0 0 74px;\n  font-size: 15px;\n}\n.input-hasTitle .input-area .weui-cells {\n  margin-top: 0;\n}\n.input-hasTitle .input-area .weui-cells .vux-x-input {\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  padding: 7px 5px 7px;\n  border: 1px solid #d9d9d9;\n}\n.input-hasTitle .input-area .weui-cells .vux-x-input .weui-input {\n  font-size: 14px;\n}\n.input-hasTitle .input-desc .item {\n  margin-left: 5px;\n  font-size: 15px;\n}\n.input-hasTitle .input-select .item {\n  font-size: 15px;\n}\n.input-hasTitle .input-select .item .weui-cells {\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  border: 1px solid #d9d9d9;\n  overflow: hidden;\n  height: 40px;\n  line-height: 40px;\n  margin-top: 0;\n}\n.input-hasTitle .input-select .item .weui-cells:before,\n.input-hasTitle .input-select .item .weui-cells:after {\n  display: none;\n}\n.input-hasTitle .input-select .item .weui-cells .normal-selector {\n  height: 38px;\n  padding-left: 5px;\n  padding-right: 15px;\n  position: relative;\n  font-size: 14px;\n  color: #8f8f8f;\n}\n.input-hasTitle .input-select .item .weui-cells .normal-selector:after {\n  content: \" \";\n  display: inline-block;\n  height: 6px;\n  width: 6px;\n  border-width: 2px 2px 0 0;\n  border-color: #C8C8CD;\n  border-style: solid;\n  -webkit-transform: matrix(0.71, 0.71, 0.71, -0.71, 0, 0);\n  transform: matrix(0.71, 0.71, 0.71, -0.71, 0, 0);\n  position: relative;\n  top: -2px;\n  position: absolute;\n  top: 50%;\n  margin-top: -4px;\n  right: 13px;\n}\n.input-hasTitle .item {\n  height: 40px;\n  line-height: 40px;\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  overflow: hidden;\n}\n/*textarea描述与选择图片*/\n.pic-area {\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  border: 1px solid #d9d9d9;\n  min-height: 63px;\n}\n.pic-area .input .weui-cells {\n  border: none;\n  overflow: visible!important;\n  height: auto!important;\n}\n.pic-area .input textarea {\n  font-size: 15px;\n}\n.pic-area .input .addImgs {\n  padding-bottom: 8px;\n}\n.pic-area .input .addImgs:after {\n  content: \"\";\n  clear: both;\n  height: 0;\n  overflow: hidden;\n  opacity: 0;\n  display: block;\n}\n.pic-area .input .addImgs a {\n  float: right;\n  margin-right: 10px;\n  margin-top: 10px;\n  width: 50px;\n  height: 50px;\n}\n.pic-area .input .addImgs a img {\n  display: block;\n  width: 100%;\n  height: 100%;\n}\n.pic-area .input .addImgs .imgAddBtn {\n  background: url(\"../../../../src/assets/img/imgAdd-btn.png\") 0 0 no-repeat;\n  background-size: 100%;\n  position: relative;\n  font-size: 0;\n  box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  -moz-box-sizing: border-box;\n  border: 1px solid #d9d9d9;\n}\n.pic-area .input .addImgs .imgAddBtn input {\n  position: absolute;\n  top: 0;\n  left: 0;\n  display: block;\n  width: 100%;\n  height: 100%;\n  line-height: 100px;\n  opacity: 0;\n}\n/*列单元*/\n.list-box .list {\n  border-bottom: 1px solid #d9d9d9;\n  padding: 10px 35px 10px 15px;\n  overflow: hidden;\n  position: relative;\n}\n.list-box .list:active {\n  background-color: #ECECEC;\n}\n.list-box .list:after {\n  content: \" \";\n  position: absolute;\n  left: auto;\n  right: 15px;\n  top: 30px;\n  bottom: auto;\n  margin-top: -5px;\n  width: 10px;\n  height: 10px;\n  background-color: transparent;\n  border-width: 2px 2px 0 0;\n  border-color: #C8C8CD;\n  border-style: solid;\n  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);\n  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);\n}\n.list-box .list .list-title,\n.list-box .list .list-desc {\n  height: 20px;\n  line-height: 20px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n.list-box .list .list-title {\n  color: #0E90E0;\n  font-size: 15px;\n}\n.list-box .list .list-desc {\n  color: #8C9195;\n}\n.list-box .list .list-s-title {\n  font-size: 12px;\n  color: #8C9195;\n  line-height: 1;\n}\n.list-box .list .list-img {\n  width: 100%;\n  height: auto;\n}\n.list-box .list .list-img img {\n  display: block;\n  width: auto;\n  height: 100px;\n  margin: 0 auto;\n}\n.list-box .list .list-head {\n  margin-top: 9px;\n}\n.list-box .list .list-head:after {\n  content: \"\";\n  clear: both;\n  height: 0;\n  overflow: hidden;\n  opacity: 0;\n  display: block;\n}\n.list-box .list .list-head a.head {\n  float: 