
import setpreview from "./setPreview"
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

// @compareTime  a,b a>b时true
 // 时间比较  后者大于前者 ，true
var compareTime = function(currentDate,nextDate,bool){ //必须以 hh:mm 格式传入
	var a = new Date(),b = new Date();// 建时间对象
	var c1 ,c2 ;
	var h = currentDate.split(":")[0];
	var m = currentDate.split(":")[1];
	a.setHours(h);
	a.setMinutes(m);
	var j = nextDate.split(":")[0];
	var k = nextDate.split(":")[1];
	b.setHours(j);
	b.setMinutes(k);
	c1 = a.getTime(),c2 =b.getTime();
	if(!bool){
		return c2>=c1?true:false; // 注：时间相等为false
	}else{
		return c2>c1?true:false;// 注：时间不相等为false
	}

};
 var compareSignTime = function(start,end,bool){ //必须以 hh:mm 格式传入
 debugger ;
	if(this.as.type(start) !== "string" || this.as.type(end) !== "string" ){
	  alert("时间格式错误");
	}

	var a = (new Date(start)).getTime() ,
	    b = (new Date(end)).getTime() ;
	 //   a1 = (new Date(this.signupDate.startTime)).getTime() ,
	 //   b1 = (new Date(this.signupDate.endTime)).getTime() ;
	//if( b1 < a ){
  //    alert("报名开始时间不能");
	//}else{

	//}
	if(bool) return a <= b ? true : false ;
  if(!bool) return a < b ? true : false ;
};
export default{
    directives: {
      TransferDom
    },
    components: {
      Popup,XButton,Flexbox,FlexboxItem,Group,Datetime,setpreview
    },
    mounted(){
      var date = new Date();
      //  this.submitData.joinStartTime = M.F.DateFormat(new Date(),"yyyy-MM-dd");

      var _self = this;
      this.showTimeSet = true ;
      this.id = this.$route.query.id ;
      this.type = this.$route.query.type ;
      this.A.post("/v1/attendance/getExtraDatas",{activityId:this.id,type:this.type}).then(function(rs){
        if(rs.status == 0 ){
            if(!rs._data.momentTimes){
            }else{
              _self.timeData = rs._data.momentTimes ;
            }
            rs._data.startDate = M.F.DateFormat(new Date(rs._data.createTime),"yyyy-MM-dd") ;
            console.log(rs._data.startDate);
            _self.signUpDate = rs._data ;
            if(rs._data.joinStartTime && rs._data.joinEndTime ){
              _self.submitData.joinStartTime = M.F.DateFormat(new Date(rs._data.joinStartTime),"yyyy-MM-dd") ;
              _self.submitData.joinEndTime = M.F.DateFormat(new Date(rs._data.joinEndTime),"yyyy-MM-dd") ;
              _self.showSignUpTime = true ;
            }
            _self.previewText =  (rs._data.previewType == "none" && "无" || rs._data.previewType == "three" && "3条" ||  rs._data.previewType == "all" && "全部" );
            delete _self.signUpDate.momentTimes ;
            delete _self.signUpDate.previewType ;
        }
      });

    },
    data(){
       return {
          id:"",
          type:"",
          timeData:[
          {
             startTime:"",
             endTime:"",
          }
          ],
          submitData:{
             joinStartTime:"",
             joinEndTime:"",
             startTime : "",
             endTime : ""
          },
          signUpDate:{
             joinStartTime:"",
             joinEndTime:"",
             startTime : "",
             endTime : ""
          } , // 报名时间
          previewText : "",
          show1 :false,
          showPreview :false,
          showTimeSet:false,
          showTimeBox :false,

          showSignUpTime:false,

       }
    }
    ,methods:{
      $addTimeSection(){
        var obj = {
             startTime:"",
             endTime:""
        };
        this.timeData.push(obj);
      },
      $delTimeSection(index){
        this.timeData.splice(index,1);
      },
      $showTimeSetting(){
         this.showTimeSet = true ;
      }
      ,
      $change(val){

      },
      $toIndex(){
          this.showTimeSet = false;
      },
      $toggleShowTimeBox(){

          this.showTimeBox = !this.showTimeBox;
      },
      $toggleSignUpTimeBox(){
         this.showSignUpTime = !this.showSignUpTime;
      },
      //打卡时间段
      $doSubmitMomentTimes(){
        var _self = this ;
        this.timeData[0].startTime = this.timeData[0].startTime.trim();
        if(!this.timeData[0].startTime || !this.timeData[0].endTime ){
          this.showTimeBox = false;
          return ;
        }
        console.log(compareTime(this.timeData[0].startTime,this.timeData[0].endTime,true));
        if(!compareTime(this.timeData[0].startTime,this.timeData[0].endTime,true)){
          _self.$emit("modalAlert","起始时间不能大于结束时间");
          return ;
        }
        this.A.post("/v1/attendance/setMomentTimes",{momentTimes : this.timeData ,activityId : this.id ,type: this.type})
       .then(function(rs){
         if(!rs.status == 0){
          _self.$emit("modalAlert",rs.msg);
         }else{
          _self.$emit("modalAlert","保存成功");
         }
       })
      },
      //报名时间段

      $doSubmitSignUpTimes(){
        var _self = this ;
        this.submitData.joinStartTime = this.submitData.joinStartTime.trim();
        if(!this.submitData.joinStartTime || !this.submitData.joinEndTime ){
          this.showSignUpTime = false ;
          return ;
        }
        console.log(compareSignTime.call(this,this.submitData.joinStartTime,this.submitData.joinEndTime));
        //if(!compareTime(this.timeData[0].startTime,this.timeData[0].endTime,true)){
        //  _self.$emit("modalAlert","起始时间不能大于结束时间");
        //  return ;
        //}
        var start = (new Date(this.submitData.joinStartTime)).getTime(),
        end = (new Date(this.submitData.joinEndTime)).getTime();
        this.A.post("/v1/attendance/joinTime",{joinStartTime : start , joinEndTime : end ,activityId : this.id ,type: this.type})
       .then(function(rs){
         if(!rs.status == 0){
          _self.$emit("modalAlert",rs.msg);
         }else{
          _self.$emit("modalAlert","保存成功");
         }
       })
      },
      //选择预览方式
      $showPreview(){
        this.showPreview = true ;
      },
      $toPreview(status){
        this.showPreview = false ;
        this.previewText =  (status == "none" && "无" || status == "three" && "3条" ||  status == "all" && "全部" );
        //this.$router.push({path: "/admin/setPreview", query: {id: this.id, type: this.type}});
      },
      $toMeet (){

       this.$router.push({path:"/admin/punchShow/meet",query:{id:this.id,type:this.type}});
      }
    }
}



